import { Helmet } from "react-helmet"

function Miokara({}) {
    return (
        <div className="bg-gray-600 rounded-xl h-fit w-full md:p-10 p-5 flex flex-col items-center mb-20">
                <img className="h-[150px]" src='./miokara.png'/>
                <div>
                    <p className="md:text-4xl text-3xl text-gray-200 font-grenze mt-3">Miokara</p>
                    <p className="md:text-xl text-lg text-gray-300 font-grenze mt-10">Equipe de gestion, accompagnement et dveloppement de projets Minecraft en tout genre.</p>
                    <p className="md:text-xl text-lg text-gray-300 font-grenze mt-3">- Dveloppements de plugins sur mesures</p>
                    <p className="md:text-xl text-lg text-gray-300 font-grenze ">- Choix et configuration de serveurs</p>
                    <p className="md:text-xl text-lg text-gray-300 font-grenze ">- Builds</p>
                    <p className="md:text-xl text-lg text-gray-300 font-grenze ">- Dveloppement de sites web / plateformes web</p>
                    <p className="md:text-xl text-lg text-gray-300 font-grenze mt-5">Une ide d'un projet ou vnement Minecraft  raliser ? Contactez-nous, nous serons ravi de vous venir en aide</p>
                    <div className="mt-5">
                        <a href="https://discord.gg/WGvfubQ3G6">
                            <img className="h-10 hover:cursor-pointer hover:scale-105 animate-bounce" src='./discorddd.png'></img>
                        </a>
                    </div>
                </div>
            </div>
    )
}

function GameEarth({}) {
    return (
        <div className="bg-gray-600 rounded-xl h-fit w-full md:p-10 p-5 flex flex-col items-center mb-20">
                <img className="h-[150px]" src='./ge.png'/>
                <div>
                    <p className="md:text-4xl text-3xl text-gray-200 font-grenze mt-3">GameEarth</p>
                    <p className="md:text-xl text-lg text-gray-300 font-grenze mt-10">GamEarth est une communaut axe sur le gaming, fonde il y a plusieurs annes. Nous misons sur notre coute et notre convivialit pour nous dmarquer des autres communauts.</p>
                    <p className="md:text-xl text-lg text-gray-300 font-grenze mt-3">Notre quipe organise des vnements avec des lots  la cl, pour complter les soires organises par les membres. En effet, ces derniers ont la possibilit de crer leurs propres soires, via un bot ddi, pour animer le serveur.</p>
                    <p className="md:text-xl text-lg text-gray-300 font-grenze ">Nous mettons  disposition de notre communaut des serveurs de jeux : Minecraft (Survie, Cratif, Skyblock, FTB), Rust, SCP: Secret Laboratory, Ark.</p>
                    <p className="md:text-xl text-lg text-gray-300 font-grenze ">Les joueurs de tous horizons sont les bienvenus !</p>
                    <div className="mt-5 flex gap-10">
                        <a href="https://discord.gamearth.fr/">
                            <img className="h-10 hover:cursor-pointer hover:scale-105 animate-bounce" src='./discorddd.png'></img>
                        </a>
                        <a href="https://twitter.com/GamEarthFR">
                            <img className="h-10 hover:cursor-pointer hover:scale-105 animate-bounce" src='./twitter.webp'></img>
                        </a>
                        <a href="https://www.instagram.com/gamearthfr_/">
                            <img className="h-10 hover:cursor-pointer hover:scale-105 animate-bounce" src='./instagram.png'></img>
                        </a>
                    </div>
                </div>
            </div>
    )
}

export default function PartenairesPage({}) {
    return (
        <div className="bg-gray-500 h-fit min-h-screen w-full flex flex-col px-5">
            <Helmet>
                <title>{'Cit de Miokara'}</title>
            </Helmet>
            <p className="lg:text-5xl text-4xl text-gray-200 font-grenze mb-10 pt-20 self-center">Nos partenaires</p>
            <div className="w-full h-full grid lg:grid-cols-2 grid-cols-1 xl:gap-20 gap-10 xl:px-20">
                <Miokara />
                <GameEarth />
            </div>
        </div>
    )
}